import Msg from '../components/Main/Msg'
import MsgImg from '../components/Main/MsgImg'
import MsgOnlyImg from '../components/Main/Img'
import BpMsg from '../components/Main/BpMsg'
import DsMsg from '../components/Main/DsMsg'
// type 0 msg type 1 msgImg type 2 Img tpye 3 bp type 4 ds
export default {
  data () {
    return {
      chatlist: [{
        content: '重金霸屏，献给未来的你你你你你啊~',
        likes: 666,
        type: 3
      }, {
        content: '每一秒钟就有我的笑容~',
        likes: 688,
        type: 4
      }, {
        content: '黑夜问白天，能不能赦免，灰色的人间',
        likes: 5,
        type: 0
      }, {
        content: '冰封的眼泪，一滴就很咸',
        likes: 15,
        type: 1
      }, {
        content: '你说你有点难追',
        likes: 666,
        type: 2
      }, {
        content: '你会怎么',
        likes: 666,
        type: 0
      }],
      bpModelVisible: false
    }
  },
  mounted () {
  },
  methods: {
    like (index) {
      this.chatlist[index].likes++
    },
    openDialog () {
      this.$bpDialog({
        title: '提示啊',
        content: '<b>我有没有加粗</b>',
        stopAuto: false,
        confirmCallback: () => {
          setTimeout(() => {
            this.$bpDialog.hide()
          }, 3000)
        }
      })
    }
  },
  components: {
    Msg,
    MsgImg,
    MsgOnlyImg,
    BpMsg,
    DsMsg
  }
}



